import {eId, eClass, darkLightMode} from '../../components/helpers/StylingHelpers';

function HomeStyle(){
    darkLightMode('authBtn');
    setStyle();
    document.getElementsByTagName('body')[0].onresize = function(){setStyle()};
}

function setStyle(){
    eId("app").className = eId("app").className.replace("container-fluid", "container").replace(" card shadow-lg", "") + " card shadow-lg";  
    eId("app").className = eId("app").className.replace(" py-5", "") + " py-5";
    
    if(window.innerWidth > window.innerHeight){
        //eId('app').style.borderRadius = parseInt(eId('app').offsetHeight) * 0.07 + 'px';
        eId("main").className = eId("main").className.replace(" mb-5", "") + " mb-5";
    } else {
        //eId('app').style.height = eId('app').offsetWidth * 1.1 + 'px';
        //eId('app').style.borderRadius = parseInt(eId('app').offsetWidth) * 0.07 + 'px';
        //eId("logo").style.width = eId('header').clientWidth * 0.8 + 'px';
    }

    eId("main").className = eId("main").className.replace(" card-body my-2", "") + " card-body my-2";
    
    setBtn('authBtn');
    setFocusBtn('focusBtn');
}

function setBtn(ref){
    let btn = eClass(ref);

    for(let i = 0; i < btn.length; i++){
        btn[i].style.width = eId('app').clientWidth * 0.12 + 'px';
        btn[i].style.height = btn[0].style.width;
        btn[i].style.borderRadius = parseInt(btn[0].style.width) * 0.20 + 'px';
        btn[i].getElementsByTagName('i')[0].style.fontSize = parseInt(btn[0].style.width) * 0.5 + 'px';
    }
}

function setFocusBtn(ref){
    let btn = eClass(ref);

    btn[0].style.width = eId('app').clientWidth * 0.18 + 'px';
    btn[0].style.height = btn[0].style.width;
    btn[0].style.borderRadius = parseInt(btn[0].style.width) * 0.20 + 'px';
    btn[0].getElementsByTagName('i')[0].style.fontSize = parseInt(btn[0].style.width) * 0.5 + 'px';
}

export default HomeStyle;